window.aliFilter = {};

import getItemAccordingToLayaut from "./get-items.js";
import { init } from "./goods.js";
import customSorter from "./customSorter.js";
// import scrollTo from "./scroller.js";
// import loadingPreloader from "./loading-preloader.js";

async function start() {
  window.aliFilter.runScroll = true;

  let timerGoods = setTimeout(function tickGoods() {
    const { childList, parentListContainer } = getItemAccordingToLayaut();

    if (childList) {
      init(Array.from(childList), parentListContainer);
    } else {
      timerGoods = setTimeout(tickGoods, 250); // (*)
    }
  }, 250);
  return true;
}
start();

///
let timerCountry = setTimeout(async function tickCountry() {
  let coutntrtChangerRef = false;
  if (document.querySelector(".switcher-info")) {
    coutntrtChangerRef = document.querySelector(".switcher-info");
  } else {
    document.querySelector(
      ".TopHeadV2_TopHeadV2__title__dt6yr.TopHeadV2_TopHeadV2__clickable__dt6yr"
    );
  }

  if (coutntrtChangerRef) {
    await getId(coutntrtChangerRef);
    customSorter();
  } else {
    timerCountry = setTimeout(tickCountry, 50); // (*)
  }
}, 50);

function getId(coutntrtChangerRef) {
  return new Promise((resolve, reject) => {
    const displayModeRef = document.querySelector(".display-mode");
    const displayMode =
      displayModeRef.childNodes[2].classList.value === "svg-icon m active";
    window.aliFilter.displayMode = displayMode;
    if (document.querySelector(".language_txt")) {
      const currentLanguage =
        document.querySelector(".language_txt").textContent;
      const cssFlag = document.querySelector(".css_flag");
      if (currentLanguage && cssFlag) {
        let newString = cssFlag.className.slice(13).toLocaleUpperCase();
        let countryID = newString;
        if (newString && countryID) {
          window.aliFilter.aliCountryID = countryID;
          window.aliFilter.domain = window.location.hostname;
          window.aliFilter.currentLanguage = currentLanguage;
          return resolve(true);
        }
      }
    }

    const countryIdObserver = new MutationObserver((mutationRecords) => {
      if (mutationRecords[0].addedNodes[0].nodeName === "SPAN") {
        const currentLanguage =
          document.querySelector(".language_txt").textContent;
        const cssFlag = document.querySelector(".css_flag");
        let newString = cssFlag.className.slice(13).toLocaleUpperCase();
        let countryID = newString;

        window.aliFilter.aliCountryID = countryID;
        window.aliFilter.domain = window.location.hostname;
        window.aliFilter.currentLanguage = currentLanguage;

        countryIdObserver.disconnect();
        resolve(true);
      }
    });

    countryIdObserver.observe(coutntrtChangerRef, {
      childList: true,
      subtree: true,
    });
  });
}

export { start };

const siteBody = document.querySelector("body");
function bodyMutationObserver() {
  let bodyObserver = new MutationObserver((mutationRecords) => {
    if (
      mutationRecords[0].removedNodes.length > 0 &&
      mutationRecords[0].removedNodes[0].nodeName === "DIV" &&
      mutationRecords[0].removedNodes[0].classList.value ===
        "next-overlay-wrapper"
    ) {
      if (window.aliFilter.currentLanguage !== "Русский") {
        // location.reload();
      }
      let item = document.querySelectorAll("[data-price]");
      const chekWiewStatus = document.querySelector(".display-mode");
      if (chekWiewStatus.childNodes[1].classList.contains("active")) {
        window.aliFilter.displayMode = false;
      } else {
        window.aliFilter.displayMode = true;
      }
      // если были добавлены новые товары
      if (!item || item.length < 15) {
        start();
      }
    }
  });

  bodyObserver.observe(siteBody, { childList: true });
}
bodyMutationObserver();
