import goodsPreloader from './goods-preloader.js';
import createObserverGoods from './goods-obverver.js';

function init(items, parentListContainer) {
    console.log('init in goods', items, parentListContainer);
    if (items.length > 0) {
        items.forEach((item) => {
            goodsPreloader(item);
            updatePrice(item);
        });
    }

    createObserverGoods(parentListContainer);
}

function updatePrice(item) {
    try {
        //span с ценой товара без доставки
        let currentPriceWithoutDelivery = null;
        if (item.querySelector('.price-current')) {
            currentPriceWithoutDelivery = item.querySelector('.price-current').textContent;
        } else if (item.querySelector('._12A8D')) {
            currentPriceWithoutDelivery = item.querySelector('._12A8D').textContent;
        } else if (item.querySelector('._13_ga._37W_B')) {
            //!! 03.12.2021
            let computedPriceStr = '';
            let priceContainer = Array.from(item.querySelectorAll('._13_ga._37W_B span'));
            if (priceContainer[priceContainer.length - 1].className === 'preloader') {
                priceContainer.pop();
            }
            priceContainer.forEach((element) => {
                computedPriceStr += element.textContent;
            });
            // //console.log("priceContainer ", computedPriceStr);
            // computedPriceStr =
            //   priceContainer[0].textContent +
            //   priceContainer[1].textContent +
            //   priceContainer[2].textContent +
            //   priceContainer[3].textContent;
            currentPriceWithoutDelivery = computedPriceStr;
            //!!
        }

        // //console.log("cur", currentPriceWithoutDelivery);
        if (!currentPriceWithoutDelivery) {
            throw new Error('empty currentPriceWithoutDelivery');
        }
        let currentPriceTextContent = currentPriceWithoutDelivery;
        let numMatch = currentPriceTextContent.match(new RegExp('([0-9\\s|,]+)[\\.\\,]([0-9\\s]+)'));
        if (!numMatch) {
            numMatch = currentPriceTextContent.match(new RegExp('([0-9\\s|,]+)'));
            if (!numMatch) {
                throw new Error('empty currentPriceWithoutDelivery');
            }
        }

        let price = '';
        if (numMatch[1]) {
            price += numMatch[1];
        }
        if (numMatch[2]) {
            price += numMatch[2];
        }

        //привоодим полученную цену к строке и убираем лишние пробелы
        let priceTostr = String(price).replace(/\s|,/g, '');

        // //console.log("price", priceTostr);
        currentPriceTextContent = currentPriceTextContent.replace(/([0-9\s|,]+)[\.\,]([0-9\s]+)/, ' {{S}} ');
        // //console.log("цена без д", currentPriceTextContent, item);
        //id товара (ru, ukr, usa...)
        let productListItem = item.querySelector('div');
        let productID = productListItem.getAttribute('data-product-id');
        //id for china
        // !!было
        let productListItemChina = false;
        if (item.href) {
            productListItemChina = item.href;
        } else {
            productListItemChina = item.querySelector('a').href;
        }
        // !!стало 03.12.2021
        // let productListItemChina = item.href;
        let idRegex = productListItemChina.match(new RegExp('[0-9]+'));
        let productIdChina = idRegex[0];
        const RU_URL = `https://aliexpress.ru/aer-api/v1/product/detail/freight?product_id=${productID}`;
        const OTHER_URL = `https://${aliFilter.domain}/aeglodetailweb/api/logistics/freight?productId=${productIdChina}&country=${aliFilter.aliCountryID}`;
        let url = '';
        let requestObj = {
            credentials: 'include',
        };
        if (window.aliFilter.currentLanguage === 'Русский') {
            url = RU_URL;
            requestObj.method = 'POST';
            let bodyObj = {
                productId: Number(productID),
                country: aliFilter.aliCountryID,
            };
            requestObj.body = JSON.stringify(bodyObj);
        } else {
            url = OTHER_URL;
            requestObj.method = 'GET';
        }
        fetch(url, requestObj)
            .then((response) => response.json())
            .then((data) => {
                // //console.log("response", data, item);
                // if (data.code !== 200 || data.methods.length !== 0 || !data.methods) {
                //   //console.log("нет доставки в выбранную страну", data.code, item, data);
                //   throw new Error("empty currentPriceWithoutDelivery");
                // }

                let amountDelivery = false;

                if (window.aliFilter.currentLanguage !== 'Русский') {
                    amountDelivery = data.body.freightResult[0].previewFreightAmount
                        ? data.body.freightResult[0].previewFreightAmount.value
                        : data.body.freightResult[0].freightAmount.value;
                } else {
                    amountDelivery = data.methods[0].amount.value;
                }
                if (amountDelivery === 0 || amountDelivery === undefined) {
                    item.setAttribute('data-price', Number(priceTostr));
                    let preloader = item.querySelector('.preloader');
                    if (preloader) {
                        preloader.remove();
                    }
                    return;
                } else {
                    let minDeliveryPrice = amountDelivery * 100;
                    // //console.log("минимальная цена доставной", minDeliveryPrice);

                    let productPriceWithDelivery = (minDeliveryPrice + Number(priceTostr)) / 100;
                    // //console.log("прайс с доставной(число)", productPriceWithDelivery);
                    currentPriceTextContent = currentPriceTextContent.replace('{{S}}', productPriceWithDelivery.toFixed(2));
                    currentPriceTextContent += ' w\\shipping';
                    let priceWictDelivery = currentPriceTextContent;

                    // НИКИТА БЫЛО
                    if (item.querySelectorAll('._13_ga._37W_B').length > 0) {
                        item.querySelector('._13_ga._37W_B').innerHTML = priceWictDelivery;
                    }

                    // СТАЛО
                    if (item.querySelectorAll('._12A8D').length > 0) {
                        item.querySelector('._12A8D').innerHTML = priceWictDelivery;
                    }

                    if (item.querySelectorAll('.price-current').length > 0) {
                        item.querySelector('.price-current').innerHTML = priceWictDelivery;
                    }

                    currentPriceWithoutDelivery = priceWictDelivery;
                    //добавляю дата атрибут на "LI" для сортировки (*100 привдит к числу без раздел.знаков)

                    item.setAttribute('data-price', Math.round(productPriceWithDelivery * 100));

                    //удаляется прелоадер
                    // let preloader = item.querySelector(".preloader");
                    if (item.querySelector('.preloader')) {
                        item.querySelector('.preloader').remove();
                    }
                }
            })
            .catch((error) => {
                console.log(error, item);
                item.setAttribute('data-price', Number(priceTostr));
                if (item.querySelector('.preloader')) {
                    item.querySelector('.preloader').remove();
                }
            });
    } catch (e) {
        console.error('Goods catch', e);
        if (item) {
            //console.log("goods item", item);
            item.setAttribute('data-price', Number(priceTostr));
            if (item.querySelector('.preloader')) {
                item.querySelector('.preloader').remove();
            }
        }
    }
}

export { init, updatePrice };
