export default function getItemAccordingToLayaut() {
    let parentListContainer = false;
    let childList = false;

    switch (window.aliFilter.currentLanguage) {
        case 'Русский':
            parentListContainer = document.querySelector('.list-items');
            childList = document.querySelectorAll('.list-item[hasctr="y"]');

            break;

        default:
            //console.log(window.aliFilter);
            if (window.aliFilter.displayMode === true) {
                parentListContainer = document.querySelector('._2AlTf');
                if (document.querySelectorAll('._2E_KG').length > 0) {
                    childList = document.querySelectorAll('._2E_KG');
                } else if (document.querySelectorAll('._3KNwG._2QFcF').length > 0) {
                    childList = document.querySelectorAll('._3KNwG._2QFcF');
                }
            } else if (window.aliFilter.displayMode === false) {
                parentListContainer = document.querySelector('.JIIxO');
                // !!было
                if (document.querySelectorAll('._1OUGS').length > 0) {
                    childList = document.querySelectorAll('._1OUGS');
                } else if (document.querySelectorAll('._3KNwG._2f4Ho').length > 0) {
                    childList = document.querySelectorAll('._3KNwG._2f4Ho');
                }
            }

            break;
    }

    return { parentListContainer, childList };
}

