import getItemAccordingToLayaut from './get-items.js';

export default function sortItems() {
    startSort();
    //console.log("status sort", window.aliFilter.status);
    let items, itemsPrices, displayMode, displayModeCheck, parentContainer;

    displayMode = [...document.querySelector('.display-mode').childNodes];
    displayModeCheck = displayMode[displayMode.length - 1].classList.contains('active');

    function swap(items, leftIndex, rightIndex) {
        var temp = items[leftIndex];
        items[leftIndex] = items[rightIndex];
        items[rightIndex] = temp;
    }

    let customPriceProp, priceProp;
    // (function () {
    //   let selectProperties = Array.from(document.querySelectorAll(".sort-item"));
    //   priceProp = selectProperties[selectProperties.length - 1];
    //   customPriceProp = priceProp.cloneNode(true);
    //   customPriceProp.id = "customPricePropId";
    //   priceProp.style.display = "none";
    //   document.querySelector(".sort-by-wrapper").appendChild(customPriceProp);
    // })();

    function startSort() {
        console.log('Start sort');
        let arrowUpCheck = false;
        let sortedItems;

        let { parentListContainer, childList } = getItemAccordingToLayaut();
        // //console.log(parentListContainer, childList);
        items = [...childList];
        console.log('ZA', parentListContainer, childList);
        itemsPrices = items.map((a) => {
            let price = Number(a.getAttribute('data-price'));
            if (price === 0) {
                console.log(a);
                console.log('attr', a.getAttribute('data-price'));
                console.log('num attr', Number(a.getAttribute('data-price')));
                debugger;
                throw new Error('0');
            }
            return price;
        });
        console.log(itemsPrices);
        return true;
        if (!arrowUpCheck) {
            sortedItems = quickSort(items, itemsPrices, 0, itemsPrices.length - 1);
        } else {
            sortedItems = [...items].reverse();
        }
        parentListContainer.innerHTML = '';

        if (parentListContainer.classList.contains('.list-items')) {
            if (displayModeCheck) {
                sortedItems.forEach((item) => {
                    parentListContainer.appendChild(item);
                });
            } else {
                for (let i = 0; i < Math.ceil(sortedItems.length / 5); i++) {
                    let justDiv = document.createElement('div');
                    for (let g = 1; g <= 5; g++) {
                        justDiv.appendChild(sortedItems[i * 5 + g - 1]);
                    }
                    parentListContainer.appendChild(justDiv);
                }
            }
        } else {
            sortedItems.forEach((item) => {
                parentListContainer.appendChild(item);
            });
        }

        arrowUpCheck = !arrowUpCheck;
    }

    function quickSort(items, itemsPrices, left, right) {
        console.log(items, itemsPrices, left, right);
        let index;
        if (itemsPrices.length > 1) {
            index = partition(items, itemsPrices, left, right);
            if (left < index - 1) {
                quickSort(items, itemsPrices, left, index - 1);
            }
            if (index < right) {
                quickSort(items, itemsPrices, index, right);
            }
        }
        return items;
    }

    function partition(items, itemsPrices, left, right) {
        let pivot = itemsPrices[Math.floor((right + left) / 2)],
            i = left,
            j = right;
        while (i <= j) {
            while (itemsPrices[i] < pivot) {
                i++;
            }
            while (itemsPrices[j] > pivot) {
                j--;
            }
            if (i <= j) {
                swap(itemsPrices, i, j);
                swap(items, i, j);
                i++;
                j--;
            }
        }
        return i;
    }
}
