var ProgressBar = require("progressbar.js");

import runScroller from "./scroller.js";

export default function loadingPreloader() {
  const message = chrome.i18n.getMessage("preloaderMessage");
  const messageNode = document.createElement("span");
  messageNode.textContent = message;
  messageNode.classList.add("ali-ext_message");
  let bgg = document.createElement("div");
  bgg.id = "ali_ext-bg";
  bgg.style.backgroundColor = "#00000099";
  bgg.style.position = "fixed";
  bgg.style.width = "100%";
  bgg.style.height = "100%";
  bgg.style.zIndex = "999999";
  bgg.style.top = 0;
  document.querySelector("body").append(bgg);
  let popup = document.createElement("div");
  popup.classList.add("ail-ext_preloader-popup");
  let prodContainer = document.querySelector(".product-container");
  let newPopup = document.createElement("div");
  newPopup.classList.add("ali-ext_preloader-new-popup");

  popup.append(newPopup);
  let close = document.createElement("span");
  close.classList.add("ail-ext_close");
  popup.append(close);
  popup.append(messageNode);

  prodContainer.append(popup);

  window.bar = new ProgressBar.Line(newPopup, {
    strokeWidth: 2,
    easing: "easeInOut",
    duration: 5000,
    color: "#ed694b",
    trailColor: "#eee",
    trailWidth: 1,
    svgStyle: { width: "100%", height: "100%", zindex: 999999 },
    text: {
      style: {
        // Text color.
        // Default: same as stroke color (options.color)
        position: "relative",
        color: "#999",
        marginTop: "5px",
        textAlign: "end",
        transform: null,
      },
      autoStyleContainer: false,
    },
    from: { color: "#ed694b" },
    to: { color: "#ED6A5A" },
    step: (state, bar) => {
      bar.setText(Math.round(bar.value() * 100) + " %");
    },
  });
  // bar.animate(1.0, () => {}); // Number from 0.0 to 1.0

  let closePopup = document.querySelector(".ail-ext_close");
  closePopup.addEventListener("click", () => {
    document.querySelector("#ali_ext-bg").remove();
    document.querySelector(".ail-ext_preloader-popup").remove();
    window.aliFilter.runScroll = false;
    runScroller(false);
  });
}
