import goodsPreloader from './goods-preloader.js';
import { updatePrice } from './goods.js';

export default function createObserverGoods(listContainer) {
    return new Promise(function (resolve, reject) {
        let observer = new MutationObserver((mutationRecords) => {
            mutationRecords.forEach((record) => {
                if (record.type === 'attributes' && record.attributeName === 'hasctr' && record.oldValue === null) {
                    updatePrice(record.target);
                    return true;
                }
                if (
                    !(
                        record.type === 'childList' &&
                        record.addedNodes.length > 0 &&
                        record.oldValue === null &&
                        record.addedNodes[0]['classList'] &&
                        !record.addedNodes[0].hasAttribute('data-price')
                    )
                ) {
                    return true;
                }

                if (record.addedNodes[0]['classList'].contains('_1OUGS')) {
                    updatePrice(record.addedNodes[0]);
                    return true;
                } else if (record.addedNodes[0]['classList'].contains('_2E_KG')) {
                    updatePrice(record.addedNodes[0]);
                    return true;
                } else if (
                    record.addedNodes[0]['classList'].contains('_3KNwG') &&
                    record.addedNodes[0]['classList'].contains('_2QFcF')
                ) {
                    updatePrice(record.addedNodes[0]);
                    return true;
                } else if (record.addedNodes[0]['classList'].contains('list-item')) {
                    updatePrice(record.addedNodes[0]);
                    return true;
                } else if (
                    record.addedNodes[0]['classList'].value === '' &&
                    record.addedNodes[0].children.length > 0 &&
                    record.addedNodes[0].children[0]['classList'].value !== 'report-btn-wrap'
                ) {
                    updatePrice(record.addedNodes[0]);
                    return true;
                } else if (
                    record.addedNodes[0]['classList'].contains('_3KNwG') &&
                    record.addedNodes[0]['classList'].contains('_2f4Ho')
                ) {
                    updatePrice(record.addedNodes[0]);
                    return true;
                }
            });
        });

        observer.observe(listContainer, {
            childList: true,
            subtree: true,
            attributes: true,
        });
        return resolve(true);
    });
}
