export default function getPriceContainer(item) {
  let priceContainer = false;

  switch (window.aliFilter.currentLanguage) {
    case "Русский":
      priceContainer = item.querySelector(".item-price-row");

      break;

    default:
      if (window.aliFilter.displayMode === true) {
        //карточки
        priceContainer = item.querySelector("._2i3yA");
      }
      //список
      else if (window.aliFilter.displayMode === false) {
        // !!было
        priceContainer = item.querySelector("._12A8D");
        // !!стало 03.12.2021
        // priceContainer = item.querySelector("._13_ga._37W_B");
      }
      break;
  }
  return priceContainer;
}
