import "./styles/preloader.css";
import getPriceContainer from "./get-price-container.js";

export default function goodsPreloader(item) {
  if (!item) {
    return true;
  }
  let priceContainer = getPriceContainer(item);
  if (!priceContainer) {
    return true;
  }

  const preloader = document.createElement("span");
  preloader.classList.add("preloader");
  priceContainer.append(preloader);
}
