const duration = 50;
export default function scrollTo(elem, callback) {
    let stop = false;

    if (stop === true) {
        return;
    }

    let fixedOffset = elem.getBoundingClientRect().top.toFixed();

    const onScroll = function (event) {
        if (event && event.isTrusted === true) {
            return true;
        }

        if (window.runScroll === false) {
            return;
        }

        fixedOffset = (elem.getBoundingClientRect().top + window.pageYOffset).toFixed();

        if (window.pageYOffset.toFixed() <= +fixedOffset + 2) {
            window.removeEventListener('scroll', onScroll);
            // чекает пока появится атрибут стоимости, вызывает callback
            let timerIdofset = setTimeout(function tick() {
                if (window.aliFilter.displayMode === false && window.aliFilter.currentLanguage === 'Русский') {
                    let items = elem.querySelectorAll('li');
                    //
                    if (items.length > 0) {
                        items = Array.from(items);
                        for (let index = 0; index < items.length; index++) {
                            const item = items[index];

                            if (item.querySelector('img')) {
                                if (item.hasAttribute('data-price') && index === items.length - 1) {
                                    if (window.aliFilter.runScroll === false) {
                                        stop = true;
                                    }
                                    if (stop === true) {
                                        return;
                                    }
                                    let { relative } = getScrollPosition();
                                    relative = relative * 1.1;
                                    window.bar.animate(relative, {
                                        duration: 100,
                                    });
                                    return callback();
                                }

                                if (!item.hasAttribute('hasctr')) {
                                    item.setAttribute('hasctr', 'y');
                                }
                            }
                        }
                        timerIdofset = setTimeout(tick, duration); // (*)
                    } else {
                        timerIdofset = setTimeout(tick, duration); // (*)
                    }
                }
                //
                else {
                    if (window.aliFilter.runScroll === false) {
                        return;
                    }
                    if (elem.querySelector('img')) {
                        if (elem.hasAttribute('data-price')) {
                            if (window.aliFilter.runScroll === false) {
                                stop = true;
                            }
                            if (stop === true) {
                                return;
                            }
                            let { relative } = getScrollPosition();
                            relative = relative * 1.1;
                            window.bar.animate(relative, {
                                duration: 100,
                            });
                            return callback();
                        }

                        if (!elem.hasAttribute('hasctr')) {
                            elem.setAttribute('hasctr', 'y');
                        }
                    } else {
                        timerIdofset = setTimeout(tick, duration); // (*)
                    }

                    timerIdofset = setTimeout(tick, duration); // (*)
                }
            }, duration);
        }
    };

    window.addEventListener('scroll', onScroll);

    onScroll();

    window.scrollTo({
        top: fixedOffset,
    });
}

function getScrollPosition() {
    var viewportHeight = Math.max(document.documentElement.clientHeight, window.innerHeight || 0); // Viewport height (px)
    var scrollPosition = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop; // Current scroll position (px)
    var documentHeight = document.body.scrollHeight; // Document height (px)
    var scrollPositionRelative = scrollPosition / (documentHeight - viewportHeight); // The document height is reduced by the height of the viewport so that we reach 100% at the bottom
    return {
        documentHeight: documentHeight,
        relative: scrollPositionRelative,
        absolute: scrollPositionRelative * documentHeight, // Yields an "average" pixel position
    };
}
