import getItemAccordingToLayaut from './get-items.js';
import loadingPreloader from './loading-preloader.js';
import scrollTo from './scroller.js';
import sortItems from './sort.js';
export default function runScroller() {
    if (window.aliFilter.runScroll === false) {
        return;
    }

    const bootomofpage = document.querySelector('.list-pagination');
    let index = 0;
    const { childList, parentListContainer } = getItemAccordingToLayaut();

    // console.log("parentListContainer", parentListContainer);

    loadingPreloader();
    if (window.aliFilter.runScroll === false) {
        return;
    }
    (function iter() {
        if (window.aliFilter.runScroll === false) {
            return;
        }

        if (index < parentListContainer.childNodes.length) {
            scrollTo(parentListContainer.childNodes[index], iter);
            index++;
        }

        if (bootomofpage.getBoundingClientRect().top < document.documentElement.clientHeight) {
            let timerId = setTimeout(function tick() {
                if (childList.at(-1).getAttribute('data-price') === null) {
                    timerId = setTimeout(tick, 250); // (*)
                } else {
                    window.scrollTo(0, 0);
                    document.querySelector('#ali_ext-bg').remove();
                    document.querySelector('.ail-ext_preloader-popup').remove();
                    sortItems();
                }
            }, 250);
        }
    })();
}
